// @flow

import React from "react"
import styled from "styled-components"
import { Flex } from "grid-styled"
import { LargeText, Text } from "components/atoms/typography"
import Container from "components/atoms/Container"

type Props = {
  id: string,
  title: string,
  description: string,
}

const StyledSection = styled.section`
  padding-top: 96px;
`

export default function SectionWithArticles({ id, title, description }: Props) {
  return (
    <StyledSection id={id}>
      <Container>
        <Flex direction="column" align="center" px={[3, 0]}>
          <LargeText pb={[4, 5]} center is="h3">
            {title}
          </LargeText>
          <Text pb={[3, 4]}>{description}</Text>
        </Flex>
      </Container>
    </StyledSection>
  )
}



