// @flow

import React from "react"
import { Flex } from "grid-styled"
import { LargeText, Text } from "components/atoms/typography"
import Container from "components/atoms/Container"
import Button from "components/molecules/Button"

type Props = {
  id: string,
  title: string,
  description: string,
  buttonText: string,
}

export default function SectionWithButton({
  id,
  title,
  description,
  buttonText,
}: Props) {
  return (
    <section id={id}>
      <Container>
        <Flex direction="column" align="center" px={4} pt={5}>
          <LargeText pb={[4, 5]} center is="h3">
            {title}
          </LargeText>
          <Text pb={[4, 5]} center>
            {description}
          </Text>
          <Button is="a" href="https://www.polidea.com/project">
            {buttonText}
          </Button>
        </Flex>
      </Container>
    </section>
  )
}



