// @flow

import React from "react"
import styled from "styled-components"
import Container from "components/atoms/Container"

type Props = {}

const StyledSection = styled.section`
  padding-top: 96px;
  padding-bottom: 32px;
  text-align: center;
`

export default function SectionWithVideo(props: Props) {
  return (
    <StyledSection>
      <Container>
        <iframe
          style={{ margin: "0 auto" }}
          width="800"
          height="450"
          src="https://www.youtube.com/embed/ljaSIrutgx4?rel=0&amp;showinfo=0"
          frameBorder="0"
          allow="autoplay; encrypted-media"
          allowFullscreen
        />
      </Container>
    </StyledSection>
  )
}



